.wp-block-create-block-larris-github-showcase {
	color: black;
	padding: 20px;
	border: 1px solid black;
	border-radius: 5px;

	display: flex;
	flex-direction: column;
	gap: 10px;

	* {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
	}
}

.wp-block-create-block-larris-github-showcase:hover {
	background-color: white;
}



.name-info-container, .github-repo {
	display: flex;
	justify-content: space-between;
}

.info-container, .info-container p{
	display: flex;
	color: black;
	align-items: center;
	gap: 5px;
}

.repo-name {
	text-transform: capitalize;
	font-size: 24px;
	display: inline-block;
	position: relative;
}

.repo-name::after {
	content: '';
	position: absolute;
	left: 0;
	right: 0;
	bottom: 1px;  
	height: 1px;
	background: black;
	opacity: 0;
	transition: opacity 0.5s ease-in-out;
}

.repo-name:hover::after {
	opacity: 1;
}

.btn-container {
	display: flex;
	gap: 10px;
}

/* Button container style */
.btn-container button {
    padding: 5px 10px;
    color: black;
    border: 1px solid black;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
	font-size: 14px;
}

/* Hover effect */
.btn-container button:hover {
    transform: scale(0.98); /* Slightly increase button size on hover */
}
